@import url(https://fonts.googleapis.com/css2?family=PT+Mono&display=swap);
.canvas{width:100%;height:100%;background-color:black}.landing-page-background{width:100vw;height:100vh}

h1{color:black;font-weight:bold;text-align:center}.about-card{display:flex;flex-direction:column;background-color:white;align-items:center;justify-content:center;border-radius:20px;width:calc(min(70vmin, 350px));height:calc(min(70vmin, 350px));position:absolute;top:calc(50% - calc(min(70vmin, 350px))/2);right:calc(50% - calc(min(70vmin, 350px))/2)}

.about-container{text-align:left;color:black}

.navbar-custom{width:100vw;display:flex;flex-direction:row;align-items:center;justify-content:stretch;transition-duration:0.5s;min-height:55px;z-index:1}.navbar{width:100vw}.navbar-item:hover{cursor:pointer;color:white}.dark{background-color:transparent;border-bottom:1px solid rgba(169,169,169,0.226);min-height:80px;color:white}.light{background-color:white;min-height:60px;color:black;border-bottom:1px solid rgba(97,97,97,0.226)}.navbar-item{min-width:150px;text-transform:capitalize;text-align:center;margin:auto 0;transition-duration:0.4s}.icon,.image-icon{display:none}

.card-horizontal{width:90%;border-radius:2px;display:grid;padding:20px}.school-container{display:flex;justify-content:space-around;align-items:center;grid-column:1 / span 2;flex-direction:row;grid-row-start:1}.card-horizontal-img{width:256px;height:140px}.courses-title{grid-row-start:2;grid-column:1 / span 2;margin:auto;margin-top:20px;margin-bottom:20px}.courses-outer-container{grid-row-start:3;grid-column:1 / span 2;display:grid;grid-gap:0.8rem;gap:0.8rem}.course-name{font-size:1rem;text-align:center;margin:0px;padding:2px}@media (min-width: 600px){.courses-outer-container{grid-template-columns:repeat(2, 1fr);grid-gap:5px;gap:5px}}@media (max-width: 600px){.school-container{flex-direction:column}.card-horizontal-text{margin:auto}.card-horizontal-img{margin:20px auto;grid-row:2}}

.date{color:black;margin-left:20px;margin-right:20px}.timeline-desc{color:white}.img-icon{width:100%;height:100%;border-radius:50%}

.project-card{display:flex;flex-direction:column;height:400px;margin:auto;min-width:250px;box-sizing:border-box;width:40%;text-align:center;background-size:cover;background-color:#f5f5f5;border:1px solid #c4c4c4;padding-bottom:10px}.header{width:100%;height:150px;object-fit:cover;border-bottom:1px solid #c4c4c4}.body{padding:10px}.body .header{font-size:1.2em;margin-bottom:40px}.body .desc{font-size:0.8em;margin-bottom:40px}.body .tech-stack{font-size:0.675em}.bottom-row{margin-top:auto;display:flex;flex-direction:row-reverse;padding-right:15px}.cards{max-width:1200px;margin-bottom:10%;display:grid;grid-gap:1rem}@media (min-width: 600px){.cards{grid-template-columns:repeat(2, 1fr)}}@media (min-width: 900px){.cards{grid-template-columns:repeat(3, 1fr)}}

html,body{margin:0;padding:0;font-family:"PT Mono", monospace;overflow-x:hidden;background-color:#fff}h1{color:black;margin:7.5%;font-size:calc(2.4rem + 2vw);font-weight:bold;text-align:center}h2{color:black;font-size:1.5rem;font-weight:bolder}p{color:#3f4952;font-size:1rem}.container-main{width:90vw;max-width:850px;display:flex;flex-direction:column;align-items:center;margin:auto}a:hover,a:visited,a:link,a:active{color:inherit;text-decoration:none}

