.navbar-custom {
  width: 100vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: stretch;
  // position: fixed;
  transition-duration: 0.5s;
  // top: 0;
  min-height: 55px;
  z-index: 1;
}

.navbar {
  width: 100vw;
  // height: 55px;
}

.navbar-item:hover {
  cursor: pointer;
  color: white;
}

.dark {
  background-color: transparent;
  border-bottom: 1px solid rgba(169, 169, 169, 0.226);
  min-height: 80px;
  color: white;
}

.light {
  background-color: white;
  min-height: 60px;
  color: black;
  border-bottom: 1px solid rgba(97, 97, 97, 0.226);
}

.navbar-item {
  min-width: 150px;
  text-transform: capitalize;
  text-align: center;
  margin: auto 0;
  transition-duration: 0.4s;
}

.icon,
.image-icon {
  display: none;
}

